.coin-detail-container {
	margin: 30px;
}

.coin-heading-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border-bottom: 1px solid var(--border);
	padding-top: 20px;
	padding-bottom: 20px;
	gap: 10px;
}

.coin-heading-container .coin-name {
	font-weight: 900;
	color: var(--blue);
}

.coin-heading-container p {
	font-size: 1rem;
	opacity: 0.9;
	margin-bottom: 20px;
}


.stats-container {
	display: flex;
	justify-content: space-around;
	align-items: center;
	gap: 40px;
}

.stats-container h2 {
	font-weight: 700;
	font-size: 1.4rem;
	margin-top: 20px;
	color: var(--blue);
}

.coin-details-heading {
	font-weight: 700 !important;
	margin-top: 20px !important;
	color: var(--blue) !important;
}

.coin-stats {
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid var(--border);
	font-size: 1rem;
	opacity: 0.9;
	padding: 20px;
}

.coin-stats-name {
	display: flex;
	gap: 10px;
	font-size: 1rem;
}

.coin-stats-value {
	font-weight: 700;
	margin-left: 20px;
}

.coin-value-statistics-heading p {
	font-size: 1rem;
	opacity: 0.9;
}


.coin-desc {
	flex: 0.5;
}

.coin-desc-link {
	display: flex;
	gap: 40px;
	margin-top: 40px;
	padding-top: 20px;
}

.coin-desc-link h2 {
	font-weight: 700;
	color: var(--blue);
}

.coin-desc-link p {
	font-size: 1rem;
	opacity: 0.9;
}

.coin-desc-link a {
	color: var(--blue);
}

.coin-desc-link h3 {
	font-weight: 700;
}

.coin-links {
	padding: 0px 20px;
	flex: 0.5;
}

.coin-link {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid var(--border);
	padding: 8px;
	position: relative;
}

.coin-link::before {
	content: "";
	position: absolute;
	left: 0;
	width: 3px;
	height: 100%;
	background-color: var(--blue);
	opacity: 0;
	transition: 0.1s;
}

.coin-link:hover::before {
	opacity: 1;
}

.link-name {
	text-transform: capitalize;
	font-size: 1rem;
	margin-bottom: 0px !important;
}

.coin-link a {
	color: var(--blue);
	font-weight: 700;
	font-size: 1rem;
	padding: 8px;
}

.coin-link:hover,
.coin-stats:hover {
	background-color: var(--bgSecondary);
}


.select-timeperiod {
	width: 200px !important;
	margin-top: 20px !important;
}


@media screen and (max-width: 1000px) {
	.stats-container {
		flex-direction: column;
	}
	.coin-desc-link {
		flex-direction: column;
	}
	.stats-container h2 {
		margin-top: 0px;
	}
}

@media screen and (max-width: 500px) {
	.coin-links {
		padding: 0px;
	}
	.coin-detail-container {
		margin: 0;
	}
}