.home-heading-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 40px;
}
.show-more {
	margin-top: 0px !important;
}

@media screen and (max-width: 500px) {
	.heading {
		margin-top: 20px;
	}
}

@media screen and (max-width: 1000px) {
	.home-title {
		font-size: 1.4rem !important;
	}
	.show-more {
		font-size: 1.3rem !important;
	}
}